<template>
  <div>
    <div class="flip-card" v-bind:class="{ active: flip }">
      <div class="flip-card-inner">
        <div class="flip-card-front" @click="flipCard">
          <div class="overlay" style="border-radius: 30px">
            <h1 class="text">    </h1>
          </div>
          <v-img
            class="image"
            :src="cardImg"
            height="100%"
            style="border-radius: 30px"
          ></v-img>
        </div>
        <div class="flip-card-back" style="border-radius: 30px">
          <h1 v-if="flip" class="text-center">{{ cardAnswer }}</h1>
        </div>
      </div>
      <div class="d-flex justify-content-center">
        <button v-if="flip" class="auth-button mx-auto" @click="goNext">
           
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "QuizCard",
  props: {
    currentQuiz: Object,
    fliped: Boolean,
  },
  data() {
    return {
      flip: false,
    };
  },
  computed: {
    cardImg: function () {
      return this.currentQuiz.path;
    },
    cardAnswer: function () {
      return this.currentQuiz.word;
    },
  },
  watch: {
    fliped(newValue) {
      //    prop  .
      if (newValue === true) {
        this.flipCard();
      }
    },
  },
  methods: {
    //  
    flipCard() {
      this.flip = !this.flip;
      this.$emit("is_fliped", true);
    },
    //   
    goNext() {
      console.log(" ");
      this.flipCard();
      this.$emit("nextCard", true);
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/style/auth-button.scss";
.flip-card {
  background-color: transparent;
  width: 600px;
  height: 400px;
  perspective: 1000px; /* Remove this if you don't want the 3D effect */
  transition: 0.5s ease;
  backface-visibility: hidden;
}

/* This container is needed to position the front and back side */
.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

/* Do an horizontal flip when you move the mouse over the flip box container */
.active .flip-card-inner {
  transform: rotateY(180deg);
}

/* Position the front and back side */
.flip-card-front,
.flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

/* Style the back side */
.flip-card-back {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: dodgerblue;
  color: white;
  font-size: 3rem;
  transform: rotateY(180deg);
}

.overlay {
  position: absolute;
  z-index: 5;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: 0.5s ease;
  background-color: rgb(0, 0, 0);
}

.flip-card-front:hover .overlay {
  opacity: 0.7;
}
</style>
